.password-input {
    position: relative;
}

.password-toggle {
    position: absolute;
    top: 75%;
    right: 10px;
    transform: translateY(-50%);
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    margin: 0;
}

